/* src/App.css */
body {
  background-color: #f0f0f0;
  font-family: Arial, sans-serif;
}

input {
  width: 300px;
  padding: 10px;
  font-size: 16px;
}

button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

h1 {
  color: #333;
}

p {
  font-size: 18px;
}
